<template>
  <div class="hello">
    <h1>{{ randomNumber }}</h1>
    <h1>{{ randomUsername }}</h1>
    <img :src=imgUrl
         class="review" />
    <img :src=imgUrl
         class="recover" />
    <button @click="fetchName">获取网名</button>
    <div v-for="item in users"
         :key="item.id">
      <el-input v-model="item.name"
                placeholder="请输入内容"></el-input>

    </div>
    <button @click="submittedData">提交信息</button>
  </div>
</template>

<script lang="ts">
import Vue from 'vue'
import axios from 'axios'
export default Vue.extend({
  name: 'HelloWorld',
  data() {
    return {
      randomNumber: 0,
      imgUrl: '',
      randomUsername: '',
      users: [
        { id: 1, name: '张三', vip: true },
        { id: 2, name: '李四', vip: false },
      ],
    }
  },
  mounted() {
    //获取图片
    this.randomNumber = Math.floor(Math.random() * 100)
    this.imgUrl = 'https://picsum.photos/id/' + this.randomNumber + '/50'
    //读取并类型校验
    const rawData = localStorage.getItem('userList') || '[]'
    const users = JSON.parse(rawData)
    if (users.length <= 0) {
      console.log(this.users)
    } else {
      this.users = users
      console.log(this.users)
    }
  },
  methods: {
    //获取网名
    async fetchName() {
      const res = await axios.get('https://cn.apihz.cn/api/zici/sjwm.php', {
        params: {
          id: 88888888,
          key: 88888888,
        },
      })
      if (res.data.msg.length > 5) {
        this.fetchName()
      } else {
        this.randomUsername = res.data.msg
      }
    },
    submittedData() {
      //深度序列化
      localStorage.setItem('userList', JSON.stringify(this.users))
    },
  },
})
</script>

